import axios from "axios";
import { api } from "@/services/config.js";

console.log(api);

export async function menus() {
  return await axios({
    method: "GET",
    url: `${api}/menu/GetMenu_List`,
    mode: "no-cors",
  }).catch((e) => {
    const { message } = e;
    return { error: true, message };
  });
}

export async function shipTypes() {
  return await axios({
    method: "GET",
    url: `${api}/menu/GetShipType_Combo`,
    mode: "no-cors",
  }).catch((e) => {
    const { message } = e;
    return { error: true, message };
  });
}

export async function saveMenus(body) {
  return await axios({
    method: "POST",
    url: `${api}/menu/Save_Menu`,
    mode: "no-cors",
    data: body,
  }).catch((e) => {
    console.log(e);
    const { message } = e;
    return { error: true, message };
  });
}
